.weather{
    place-self: center;
    padding: 40px;
    border-radius: 10px;
    background-color: rgb(0, 0, 128);
    display: flex;
    flex-direction: column;
    align-items: center;
}
.search-bar{
    display: flex;
    align-items: center;
    gap: 12px;
}
.search-bar input{
    height:50px ;
    border: none;
    outline: none;
    border-radius:40px;
    padding-left: 25px;
    color: rgb(99, 99, 97) ;
    background:rgb(245, 245, 220);
    font-size: 18px;
}
.search-bar img{
   width: 50px;
   padding: 15px;
   border-radius: 50%;
   color: rgb(186, 186, 184);
   background:rgb(245, 245, 220);
   cursor: pointer;
}
.weather-icon{
    width: 150px;
    margin: 30px 0;
}
.temperature{
    color: aliceblue;
    font-size: 70px;
    line-height: 1;
}
.location{
    color: aliceblue;
    font-size: 40px;
}
.weather-data{
    width: 100%;
    margin-top: 40px;
    color: aliceblue;
    display: flex;
    justify-content: space-between;
}
.weather-data .components{
    display: flex;
    align-items: flex-start;
    gap: 10px;
    font-size: 22px;
}
.weather-data .components span{
    display: block;
    font-size: 16px;
}
.weather-data .components img{
    width: 26px;
    margin-top: 10px;
}
.weather-data .components .wdata{
    padding-left: 50px;
    font-size: 25px;
    font-weight: 500;
}